.App {
  text-align: center;
}

@keyframes beat {
  0% {
    transform: scale(1);
  }
  7% {
    transform: scale(1.05);
  }
  14% {
    transform: scale(1);
  }

  30% {
    transform: scale(1);
  }
  35% {
    transform: scale(1.05);
  }
  40% {
    transform: scale(1);
  }
  60% {
    transform: scale(1);
  }
  65% {
    transform: scale(1.15);
  }
  70% {
    transform: scale(1.0);
  }

  100% {
    transform: scale(1);
  }
}

.logo {
  height: 40vmin;
  pointer-events: none;
  animation: beat 1s ease-in-out infinite;
  path {
    fill: rgb(153, 10, 10);
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #0daf7f;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #232323;
}

.time-label {
  font-size: calc(20px + 3vmin);
  font-weight: 500;
}

.text {
  font-size: calc(10px + 2vmin);
}
