@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  overflow: hidden;
  font-family: 'Dancing Script', cursive, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center}@-webkit-keyframes beat{0%{-webkit-transform:scale(1);transform:scale(1)}7%{-webkit-transform:scale(1.05);transform:scale(1.05)}14%{-webkit-transform:scale(1);transform:scale(1)}30%{-webkit-transform:scale(1);transform:scale(1)}35%{-webkit-transform:scale(1.05);transform:scale(1.05)}40%{-webkit-transform:scale(1);transform:scale(1)}60%{-webkit-transform:scale(1);transform:scale(1)}65%{-webkit-transform:scale(1.15);transform:scale(1.15)}70%{-webkit-transform:scale(1);transform:scale(1)}100%{-webkit-transform:scale(1);transform:scale(1)}}@keyframes beat{0%{-webkit-transform:scale(1);transform:scale(1)}7%{-webkit-transform:scale(1.05);transform:scale(1.05)}14%{-webkit-transform:scale(1);transform:scale(1)}30%{-webkit-transform:scale(1);transform:scale(1)}35%{-webkit-transform:scale(1.05);transform:scale(1.05)}40%{-webkit-transform:scale(1);transform:scale(1)}60%{-webkit-transform:scale(1);transform:scale(1)}65%{-webkit-transform:scale(1.15);transform:scale(1.15)}70%{-webkit-transform:scale(1);transform:scale(1)}100%{-webkit-transform:scale(1);transform:scale(1)}}.logo{height:40vmin;pointer-events:none;-webkit-animation:beat 1s ease-in-out infinite;animation:beat 1s ease-in-out infinite}.logo path{fill:#990a0a}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#0daf7f;min-height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;color:#232323}.time-label{font-size:calc(20px + 3vmin);font-weight:500}.text{font-size:calc(10px + 2vmin)}

